.skillsBlock {
    min-height: 90vh;
    background-color: #212428;
    display: flex;
    align-items: center;
}

.skillsWrapper {
    height: 100%;
    width: 90%;
    /*border: 1px solid black;*/
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #c4cfde;
    padding-bottom: 100px;
}

.skillContainer {
    height: 80%;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

h2 {
    /*border: 1px solid black;*/
    margin-top: 0 ;
}
